/* Вся секция RSVP — белый фон, контент по центру, как на скрине */
.confirm-page {
  background: #ffffff;
  color: #000000;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding: 80px 20px 120px;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.confirm-inner {
  max-width: 700px;
  width: 100%;
  margin: 0 auto;
  color: #000;
}

/* Форма вся колонкой, но не центрируем текст формы полностью,
   а делаем как у макета: заголовок по центру, поля слева */
.rsvp-form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

/* Заголовок сверху — каллиграфический шрифт */
.rsvp-title {
  text-align: center;
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: clamp(2rem, 1rem + 2vw, 2.4rem);
  font-weight: 400;
  line-height: 1.2;
  color: #000;
  margin: 0 0 40px;
  letter-spacing: 0;
  text-rendering: optimizeLegibility;
}

/* Общий блок каждого поля */
.form-block {
  width: 100%;
  max-width: 600px;
  margin: 0 auto 28px;
  display: flex;
  flex-direction: column;
  text-align: left;
  color: #000;
  font-size: 1rem;
  line-height: 1.4;
}

/* Лейблы над полями */
.field-label {
  display: block;
  font-size: 1.05rem;
  font-weight: 400;
  margin-bottom: 8px;
  color: #000;
  font-family: "Playfair Display", Georgia, serif;
}

/* Поле ввода имени */
.text-input {
  width: 100%;
  border: 1px solid #000;
  border-radius: 0;
  background: #fff;
  font-size: 1rem;
  line-height: 1.4;
  padding: 12px 14px;
  font-family: "Playfair Display", Georgia, serif;
  outline: none;
}

.text-input:focus {
  box-shadow: 0 0 0 2px rgba(0,0,0,0.1);
}

/* вопрос "будете ли вы присутствовать..." */
.field-question {
  font-size: 1.1rem;
  font-weight: 400;
  margin-bottom: 12px;
  font-family: "Playfair Display", Georgia, serif;
  color: #000;
}

/* каждая строка-радио */
.radio-line {
  font-family: "Playfair Display", Georgia, serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.4;
  color: #000;
  display: flex;
  align-items: flex-start;
  gap: 8px;
  margin-bottom: 10px;
  cursor: pointer;
}

.radio-line input[type="radio"] {
  margin-top: 3px;
  accent-color: #000;
}

/* селект количества гостей */
.select-input {
  width: 140px;
  border: 1px solid #000;
  border-radius: 0;
  background: #fff;
  font-size: 1rem;
  line-height: 1.4;
  padding: 10px 12px;
  font-family: "Playfair Display", Georgia, serif;
  outline: none;
}

.select-input:focus {
  box-shadow: 0 0 0 2px rgba(0,0,0,0.1);
}

.help-hint {
  font-size: 0.9rem;
  line-height: 1.4;
  color: rgba(0,0,0,0.6);
  margin-top: 8px;
  font-family: "Playfair Display", Georgia, serif;
  font-style: italic;
}

/* чёрная кнопка "отправить" в стиле тильды */
.submit-btn-classic {
  appearance: none;
  border: 1px solid #000;
  background: #000;
  color: #fff;
  font-family: "Playfair Display", Georgia, serif;
  font-size: 1rem;
  line-height: 1.2;
  font-weight: 400;
  text-transform: lowercase;
  padding: 14px 26px;
  min-width: 200px;
  cursor: pointer;
  margin: 20px auto 0;
  display: block;
  text-align: center;
  transition: all 0.25s ease;
}

.submit-btn-classic:hover {
  background: #fff;
  color: #000;
}

/* блок "спасибо" после отправки */
.thanks-block {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  font-family: "Playfair Display", Georgia, serif;
  color: #000;
  padding-top: 40px;
  padding-bottom: 80px;
}

.thanks-title {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: clamp(2rem, 1rem + 2vw, 2.4rem);
  font-weight: 400;
  line-height: 1.2;
  color: #000;
  margin-bottom: 24px;
}

.thanks-note {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 12px;
}

.thanks-extra {
  font-size: 0.95rem;
  line-height: 1.5;
  color: rgba(0,0,0,0.75);
  font-style: italic;
}

/* адаптив */
@media (max-width: 480px) {
  .confirm-page {
    padding: 60px 16px 80px;
  }

  .rsvp-title {
    font-size: 2rem;
    margin-bottom: 32px;
  }

  .field-question {
    font-size: 1rem;
  }

  .text-input {
    font-size: 1rem;
    padding: 10px 12px;
  }

  .radio-line {
    font-size: 0.95rem;
  }

  .select-input {
    width: 120px;
    font-size: 0.95rem;
    padding: 8px 10px;
  }

  .submit-btn-classic {
    font-size: 1rem;
    padding: 12px 20px;
    min-width: 180px;
  }
}
