/* Общий контейнер */
.invite {
 background: #ffffff;          /* тёплый молочный фон */
  color: #3a3a3a;
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 24px 16px 50px;
  
}

/* "Wedding day" — теперь крупнее */
.invite-title {
  font-weight: 700;
  letter-spacing: 0.04em;
  color: #2f2f2f;
  margin-bottom: 20px;
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
}

.invite-title.big {
  font-size: clamp(1.6rem, 1rem + 2vw, 2rem); /* на телефоне крупно, но не кричит */
  line-height: 1.2;
}

.photo-with-names {
  position: relative;
  width: 100%;
  max-width: 360px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.28);
  margin-bottom: 28px;
  aspect-ratio: 3 / 4;
  background: #000;
}

.photo-with-names img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  transform: scale(1.03);
  filter: brightness(0.95) contrast(1.05) saturate(1.05);
}

/* Текст поверх фото */
.names-overlay {
  position: absolute;
  top: 10px;             /* отступ сверху */
  left: 70%;
  transform: translateX(-50%);
  color: #fff;           /* белый текст */
  text-align: center;
  font-family: "Great Vibes", cursive;
  font-size: 2rem;       /* можно регулировать размер */
  font-weight: 400;
  text-shadow: 0 3px 8px rgba(0,0,0,0.5); /* лёгкая тень, чтобы текст читался */
}

.names-overlay span {
  display: inline-block;
  margin: 0 4px;
}


/* Фото — одно, большое, с мягкими краями */
.photo-full {
  width: 100%;
  max-width: 360px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 30px 80px rgba(0, 0, 0, 0.28);
  margin-bottom: 28px;
  position: relative;
  isolation: isolate;
  aspect-ratio: 3 / 4;        /* вертикально, красиво для портрета на мобилке */
  background: #000;
}

.photo-full img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  transform: scale(1.03);
  /* чуть-чуть кинематографичнее */
  filter: brightness(0.95) contrast(1.05) saturate(1.05);
}

/* Лёгкий затемнённый fade внизу фото (делает его аккуратным и напоминает печатную работу) */
.photo-full::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to bottom,
    rgba(0,0,0,0) 60%,
    rgba(0,0,0,0.25) 100%
  );
  pointer-events: none;
}




