/* Вся страница с таймером */
.countdown-page {
  background: #ffffff;
  color: #000000;
  width: 100%;

  /* убираем принудительную высоту экрана */
  /* min-height: 100vh; */

  /* было: padding: 80px 20px 120px; */
  padding: 0 20px 0;

  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

/* Контейнер с двумя колонками */
.countdown-inner {
  width: 100%;
  max-width: 1000px;

  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: start;
  gap: 40px;
}

/* Левая колонка (текст + таймер) */
.countdown-info {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #000;
  max-width: 480px;
  margin: 0 auto;
}

/* Заголовок "До свадьбы осталось" — рукописный стиль */
.countdown-heading {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: clamp(2rem, 1.2rem + 1vw, 2.6rem);
  font-weight: 400;
  line-height: 1.2;
  color: #000;
  margin-bottom: 28px;
  text-align: left;
  letter-spacing: 0;
}

/* Сетка таймера (десктоп) */
.timer-grid {
  display: grid;
  grid-template-columns: repeat(4, minmax(60px, 1fr));
  gap: 16px;
  max-width: 360px;
}

/* Каждая "карточка" таймера */
.timer-box {
  border: 1px solid rgba(0,0,0,0.15);
  border-radius: 12px;
  padding: 16px 12px 14px;
  text-align: center;
  background: #fff;
  box-shadow:
    0 30px 60px rgba(0,0,0,0.06),
    0 1px 1px rgba(0,0,0,0.4) inset;
  min-width: 60px;
  flex-shrink: 0; /* пригодится в мобайл-режиме */
}

/* Значение числа */
.timer-number {
  font-size: clamp(1.6rem, 1rem + 1vw, 2rem);
  line-height: 1.2;
  font-weight: 400;
  color: #000;
  font-family: "Playfair Display", Georgia, serif;
  text-align: center;
  min-width: 2ch;
  transition: transform 0.4s;
}

/* подпись под числом */
.timer-caption {
  margin-top: 8px;
  font-size: 0.7rem;
  line-height: 1.2;
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, sans-serif;
  font-weight: 400;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: rgba(0,0,0,0.7);
  text-align: center;
}

/* Мягкая анимация смены числа */
.flip {
  animation: flipOnce 0.6s ease;
}

@keyframes flipOnce {
  0%   { transform: rotateX(0deg); }
  50%  { transform: rotateX(180deg); }
  100% { transform: rotateX(360deg); }
}

/* Дата / место снизу слева */
.countdown-date {
  margin-top: 32px;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.4;
  color: #000;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
  letter-spacing: 0.02em;
}

.countdown-place {
  margin-top: 6px;
  font-size: 0.95rem;
  line-height: 1.4;
  color: rgba(0,0,0,0.7);
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, sans-serif;
}

/* Правая колонка — карточка с фото */
.countdown-photo-card {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.photo-frame {
  background: #fff;
  border: 1px solid rgba(0,0,0,0.15);
  border-radius: 16px;
  padding: 16px;
  box-shadow:
    0 40px 80px rgba(0,0,0,0.07),
    0 1px 1px rgba(0,0,0,0.4) inset;
  max-width: 360px;
  width: 100%;
}

.photo-img {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 12px;
  object-fit: cover;
  object-position: center;
  filter: brightness(1) contrast(1.03);
}

/* адаптив — на телефоне:
   1 колонка
   таймер сверху, фото снизу
   никаких паддингов сверху/снизу
   таймер = одна строка с горизонтальным скроллом
*/
@media (max-width: 800px) {
  .countdown-inner {
    grid-template-columns: 1fr;
    max-width: 480px;
  }

  .countdown-info {
    text-align: center;
    align-items: center;
  }

  .countdown-heading {
    text-align: center;
  }

  /* timer-grid на мобилке превращаем в горизонтальную ленту без переносов */
  .timer-grid {
    display: flex;
    flex-wrap: nowrap;
    gap: 16px;
    max-width: none;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: none; /* Firefox скрыть скроллбар */
  }

  .timer-grid::-webkit-scrollbar {
    display: none; /* Safari/Chrome скрыть скроллбар */
  }

  .countdown-date,
  .countdown-place {
    text-align: center;
  }

  .countdown-photo-card {
    margin-top: 40px;
  }
}

/* на очень маленьких экранах (менее 400px)
   НЕ разбиваем таймер на 2 строки. просто оставляем тот же flex-row + scroll.
   значит убираем твой старый repeat(2,...) вариант вообще. */
@media (max-width: 400px) {
  .timer-grid {
    /* уже flex из предыдущего media,
       тут просто подстрахуем, чтоб не сломалось */
    display: flex;
    flex-wrap: nowrap;
    gap: 14px;
    overflow-x: auto;
    scrollbar-width: none;
  }

  .timer-grid::-webkit-scrollbar {
    display: none;
  }
}
