/* Вся секция с единственной фотографией */
.gallery-section {
  background: #ffffff;
  width: 100%;
  display: flex;
  justify-content: center;

  /* убираем любые внешние поля сверху и снизу */
  padding: 0;

  /* не делаем min-height:100vh, чтобы не было пустого пространства */
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
  color: #000;
}

/* Рамка вокруг фото, как "распечатанный снимок" в альбоме */
.gallery-frame {
  max-width: 420px;
  width: 100%;
  margin: 0 auto;

  background: #fff;
  border: 1px solid rgba(0,0,0,0.15);
  border-radius: 16px;
  box-shadow:
    0 40px 80px rgba(0,0,0,0.07),
    0 1px 1px rgba(0,0,0,0.4) inset;

  padding: 16px;

  opacity: 0;
  transform: translateY(30px);
  transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
}

.gallery-frame-visible {
  opacity: 1;
  transform: translateY(0);
}

/* Само фото */
.gallery-photo-wrap {
  position: relative;
  width: 100%;
  border-radius: 12px;
  overflow: hidden;
  background: #000;
  aspect-ratio: 3 / 4; /* вертикальная карточка; можно сменить на 4 / 5 если нужно более вытянуто */
}

.gallery-photo {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

/* Мобильная адаптация — тоже без доп. паддингов сверху/снизу */
@media (max-width: 480px) {
  .gallery-section {
    padding: 0;
  }

  .gallery-frame {
    max-width: 360px;
    border-radius: 14px;
    padding: 14px;
  }

  .gallery-photo-wrap {
    border-radius: 10px;
  }
}

/* На очень больших экранах пусть карточка не теряется */
@media (min-width: 1200px) {
  .gallery-frame {
    max-width: 480px;
  }
}
