/* базовая страница */
.story-page {
  width: 100%;
  min-height: 100vh;
  background: #ffffff;
  color: #000000;
  display: flex;
  justify-content: center;

  /* было: padding: 80px 20px 120px; */
  padding: 80px 20px 0;

  margin-top: -120px;

  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
}

/* контейнер */
.story-inner {
  max-width: 520px;
  width: 100%;
  text-align: center;

  opacity: 0;
  transform: translateY(30px);
  transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
}

.story-visible {
  opacity: 1;
  transform: translateY(0);
}

/* рукописный заголовок */
.story-heading {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.2;
  color: #000000;
  margin-bottom: 24px;
}

/* основной текст */
.story-text {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 400;
  color: #000000;
  margin: 0 auto 24px;
  max-width: 480px;
}

/* дата крупно */
.story-date {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 1.3;
  color: #000000;
  margin-bottom: 32px;
}

/* календарь */
.calendar-block {
  text-align: center;
  color: #000000;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.calendar-month {
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 0.08em;
  margin-bottom: 16px;
  color: #000000;
}

/* таблица календаря */
.calendar-table {
  margin: 0 auto;
  border-collapse: collapse;
  font-size: 0.9rem;
  line-height: 1.4;
  font-weight: 400;
  color: #000000;
}

.calendar-table th {
  font-weight: 400;
  padding-bottom: 8px;
  font-size: 0.7rem;
  text-transform: lowercase;
  color: #555555;
}

.calendar-table td {
  width: 32px;
  height: 32px;
  text-align: center;
  vertical-align: middle;
  color: #000000;
  position: relative;
  font-size: 0.9rem;
  line-height: 32px;
}

/* ячейка с сердцем */
.calendar-heart-cell {
  padding: 0;
}

.heart-wrapper {
  position: relative;
  width: 33px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.heart-icon {
  position: absolute;
  width: 32px;
  height: 28px;
  top: 2px;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: block;
}

.heart-day {
  position: relative;
  z-index: 2;
  font-size: 0.8rem;
  line-height: 1;
  font-weight: 500;
  color: #ffffff;
  display: block;
  transform: translateY(2px);
}

/* адаптив */
@media (max-width: 480px) {
  .story-page {
    /* было: padding: 60px 20px 80px; */
    padding: 60px 20px 0;
  }

  .story-heading {
    font-size: 1.6rem;
    margin-bottom: 20px;
  }

  .story-text {
    font-size: 0.95rem;
    line-height: 1.5;
    margin-bottom: 20px;
  }

  .story-date {
    font-size: 1.4rem;
    margin-bottom: 28px;
  }

  .calendar-month {
    font-size: 0.9rem;
    margin-bottom: 14px;
  }

  .calendar-table {
    font-size: 0.8rem;
  }

  .calendar-table th {
    font-size: 0.65rem;
  }

  .calendar-table td {
    width: 30px;
    height: 30px;
    line-height: 30px;
    font-size: 0.8rem;
  }

  .heart-wrapper {
    width: 30px;
    height: 30px;
  }

  .heart-icon {
    width: 30px;
    height: 26px;
    top: 2px;
  }

  .heart-day {
    font-size: 0.75rem;
  }
}
