/* Вся секция */
.program-section {
  width: 100%;
  background: #ffffff;
  color: #010101;
  display: flex;
  justify-content: center;

  /* было: min-height: 100vh; убираем, чтобы не растягивало экран пустотой */
  /* min-height: 100vh; */

  /* было: padding: 48px 16px 80px; убираем верх/низ */
  padding: 0 16px 0;

  box-sizing: border-box;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;

  /* margin-top: -0px; эквивалентно 0; можно просто 0 */
  margin-top: 0;
}

/* Внутренний контейнер */
.program-wrapper {
  width: 100%;
  max-width: 480px; /* мобильный формат как у сторис */
  position: relative;
  text-align: center;
  box-sizing: border-box;
  color: #000000;
}

/* Заголовок */
.program-header {
  margin-bottom: 40px;
  color: #000000;
  text-align: center;
  font-family: "Playfair Display", Georgia, serif;

  /* по умолчанию сверху теперь нет паддингов из секции,
     так что если хочешь вообще без воздуха с предыдущим блоком,
     можешь обнулить и внутренний отступ тоже */
  /* padding-top: 0; */
}

.program-title {
  font-size: clamp(1.6rem, 1rem + 2vw, 2rem);
  font-weight: 300;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: #000000;
  line-height: 1.2;
}

.program-subtitle {
  font-size: 0.9rem;
  font-weight: 300;
  letter-spacing: 0.05em;
  color: rgba(0, 0, 0, 0.8);
  line-height: 1.4;
  margin-top: 8px;
  font-family: "Poppins", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", sans-serif;
}

/* Таймлайн-обёртка */
.timeline {
  position: relative;
  width: 100%;
  box-sizing: border-box;

  /* раньше было padding: 0 8px; оставим так, это не внешний отступ секции,
     это просто горизонтальный внутренний отступ контента */
  padding: 0 8px;
}

/* Вертикальная линия по центру */
.timeline-center-line {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 2px;
  background: linear-gradient(
    to bottom,
    rgba(19, 18, 18, 0.4) 0%,
    rgba(27, 26, 26, 0.1) 100%
  );
  transform: translateX(-50%);
  pointer-events: none;
}

/* Одна строка расписания (левая часть | маркер | правая часть) */
.timeline-row {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: flex-start;
  margin-bottom: 48px;
  position: relative;
  color: #000000;
}

/* Левая и правая части */
.timeline-side {
  display: flex;
  flex-direction: column;
  line-height: 1.35;
  font-family: "Playfair Display", Georgia, serif;
  color: #000000;
  padding: 0 8px;
  box-sizing: border-box;
  min-height: 80px;
}

.timeline-side.left {
  text-align: right;
  align-items: flex-end;
}
.timeline-side.right {
  text-align: left;
  align-items: flex-start;
}

/* Время */
.time {
  font-size: 2rem;
  font-weight: 300;
  color: #000000;
  line-height: 1.2;
  text-shadow: 0 0 12px rgba(255, 255, 255, 0.2);
  margin-bottom: 12px;
  font-family: "Playfair Display", Georgia, serif;
}

/* Описание под временем */
.desc {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: flex-start;
  gap: 8px;
  font-size: 1rem;
  font-weight: 300;
  color: rgba(0, 0, 0, 0.85);
  max-width: 160px;
  line-height: 1.4;
  text-align: inherit;
  font-family: "Poppins", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", sans-serif;
}

.icon {
  font-size: 1.1rem;
  line-height: 1.1rem;
  min-width: 1.1rem;
  opacity: 0.9;
}

.text {
  display: inline-block;
  white-space: pre-line;
}

/* Центральный маркер-ромб */
.timeline-marker {
  position: relative;
  display: flex;
  justify-content: center;
  padding-top: 8px;
}

.diamond {
  width: 12px;
  height: 12px;
  background: #000000;
  transform: rotate(45deg);
  box-shadow: 0 0 16px rgba(67, 58, 58, 0.6);
  border-radius: 2px;
}

/* адаптив */
@media (max-width: 400px) {
  /* ничего не добавляем сверху/снизу в мобилке,
     секция уже без паддингов, так что просто НЕ вводим новые */

  .program-title {
    font-size: 1.4rem;
    letter-spacing: 0.12em;
  }

  .program-subtitle {
    font-size: 0.8rem;
  }

  .timeline-row {
    margin-bottom: 40px;
  }

  .time {
    font-size: 1.6rem;
    margin-bottom: 10px;
  }

  .desc {
    font-size: 0.9rem;
    max-width: 140px;
    line-height: 1.35;
    gap: 6px;
  }

  .diamond {
    width: 10px;
    height: 10px;
  }
}
