.address-page {
  width: 100%;
  background: #ffffff;
  color: #000000;
  display: flex;
  justify-content: center;
  padding: 80px 20px 120px;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.address-inner {
  max-width: 480px;
  width: 100%;
  text-align: center;
  margin: 0 auto;
  color: #000;
}

/* "Адрес свадьбы" — рукописный шрифт */
.address-title {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-weight: 400;
  font-size: clamp(2rem, 1rem + 2vw, 2.4rem);
  line-height: 1.2;
  margin: 0 0 16px;
  color: #000;
  letter-spacing: 0;
  text-rendering: optimizeLegibility;
}

/* мини-иконка колец */
.address-icon {
  margin-bottom: 24px;
}
.rings-mini {
  width: 42px;
  height: auto;
  stroke: #000;
  fill: none;
  display: inline-block;
}

/* блок с адресом */
.address-textblock {
  margin-bottom: 28px;
  color: #000;
  line-height: 1.5;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.hall-name {
  font-size: 1.1rem;
  margin: 0 0 8px;
  font-weight: 400;
}
.hall-name span {
  font-style: italic;
  font-weight: 500;
  letter-spacing: 0.02em;
}

.hall-address {
  font-size: 1rem;
  margin: 0;
  font-weight: 400;
  color: #000;
}

/* кнопка */
.address-actions {
  display: flex;
  justify-content: center;
  margin-top: 24px;
  margin-bottom: 16px;
  font-family: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, sans-serif;
}

.addr-btn {
  cursor: pointer;
  text-decoration: none;
  background: #000;
  color: #fff;
  font-size: 0.9rem;
  line-height: 1.2;
  font-weight: 400;
  padding: 12px 20px;
  border-radius: 999px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  border: 1px solid #000;
  box-shadow: 0 12px 24px rgba(0,0,0,0.18);
  transition: all 0.25s ease;
}

.addr-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 16px 28px rgba(0,0,0,0.28);
}


/* адаптив */
@media (max-width: 500px) {
  .address-page {
    padding: 60px 16px 80px;
  }

  .address-title {
    font-size: 2rem;
  }

  .hall-name {
    font-size: 1rem;
  }

  .hall-address {
    font-size: 0.95rem;
    line-height: 1.4;
  }

  .rings-mini {
    width: 36px;
  }

  .addr-btn {
    font-size: 0.8rem;
    padding: 11px 18px;
    letter-spacing: 0.1em;
  }
}
